<template>
    <div class="public_content">
        <div class="public_title"><span>编辑门店资料</span></div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

            <el-form-item label="门店logo" prop="logo">
               <upload-img v-if="dataSuccess" :data='ruleForm.logo' @onSuccess="changeLogo"></upload-img>
            </el-form-item>

            <el-form-item label="门店名称" prop="company_name">
                <el-input v-model="ruleForm.company_name" style="width: 200px"></el-input>
            </el-form-item>

            <el-form-item label="门店位置" prop="region" v-if="ruleForm.id">
                 <v-address :pregion='ruleForm.region' :pestate='ruleForm.estate' :paddress='ruleForm.address' @emitChange='emitChange'/>
            </el-form-item>


            <el-form-item label="门店电话" prop="tel">
                <el-input v-model="ruleForm.tel" style="width: 200px"></el-input>
            </el-form-item>

            <el-form-item label="门店介绍" prop="introduce" style="margin-bottom: 120px">
              <el-input style="width: 700px" type="textarea" :rows="6"  v-model="ruleForm.introduce">
              </el-input>
            </el-form-item>

            <el-button type="primary" round @click="submitForm" class="public_submit">确认提交</el-button>
         </el-form>
    </div>
</template>

<script>
import { editCompanyInfo, getCompanyInfo } from "@/api/shop";
import uploadImg from "@/components/upload/uploadimg";
import vAddress from "@/components/address/index";
export default {
  components: {
    uploadImg,
    vAddress
  },
  data() {
    return {
      dataSuccess: false,
      ruleForm: {
        logo: "",
        company_name: "",
        region: null,
        estate: null,
        address: "",
        tel: "",
        introduce: ""
      },
      rules: {
        // logo: [{ required: true, message: "请上传门店logo" }],
        company_name: [
          { required: true, message: "请输入门店名称", trigger: "change" }
        ],
        tel: [{ required: true, message: "请输入门店电话", trigger: "change" }]
        // region: [{ required: true, message: "请选择门店区域" }],
        // estate: [{ required: true, message: "请选择门店地段" }],
        // address: [{ required: true, message: "请输入详细地址" }]
      }
    };
  },
  created() {
    getCompanyInfo().then(res => {
      this.dataSuccess = true
      if (res.data.code == 1) {
        
        this.ruleForm = res.data.data[0];
      }
    });
  },
  methods: {
    submitForm() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          editCompanyInfo(this.ruleForm).then(res => {
            if (res.data.code == 1) {
              this.$message.success(res.data.msg)
              this.$router.go(0)
            }
          });
        } else {
          this.$message.error("填写错误");
          return false;
        }
      });
    }, 
    changeLogo(data) { 
      this.ruleForm.logo = data;
    },
    /* 地址接收 */
    emitChange(data) {
      this.ruleForm.region = data.region;
      this.ruleForm.estate = data.estate;
      this.ruleForm.address = data.address;
    }
  }
};
</script>

<style lang='scss' scoped>
</style>



